body {
  background-image: url("../images/background.png");
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover; }

.bubble-box {
  fill: white;
  border: 10px;
  width: 42%;
  margin: 40px;
  background-color: rgba(255, 255, 255, 0.93);
  border-radius: 12px;
  padding: 35px;
  padding-bottom: 50px;
  animation: initial-slide-up 0.9s forwards ease-in-out; }

.bubble-button {
  font-family: "Century Gothic";
  font-weight: normal;
  font-size: 10px;
  opacity: 0;
  animation: initial-slide-right 0.5s forwards 1s ease-in-out; }
  .bubble-button a {
    text-decoration: none; }
  .bubble-button #COLLABORATE {
    color: white;
    background-color: #519cff;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 25px;
    padding-right: 25px;
    border-radius: 30px;
    margin-left: 72%; }

.bubble-social {
  background-color: white;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  margin-left: 40px;
  margin-top: 70px;
  margin-bottom: 20px; }
  .bubble-social a {
    transition: transform 0.15s forease-in-out; }
  .bubble-social img {
    position: relative;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 40px;
    height: 40px; }
  .bubble-social:hover {
    transform: scale(1.1); }

.header {
  font-family: "Century Gothic";
  color: white;
  margin: 25px 25px 60px; }
  .header #KVIN {
    font-size: 30.5px;
    font-weight: normal; }
  .header #TANAKA {
    font-size: 30.5px;
    font-weight: bold; }
  .header #SOFTWARE-DEVELOPER {
    font-size: 10px;
    letter-spacing: 1px;
    padding-left: 2px; }
  .header nav {
    display: none;
    position: absolute;
    left: 30%;
    top: 45px; }
  .header a {
    text-decoration: none;
    font-weight: bold;
    font-size: 11px;
    color: white;
    padding-top: 12px;
    padding-bottom: 12px;
    padding-left: 24px;
    padding-right: 24px;
    border-radius: 40px;
    background-color: rgba(191, 191, 191, 0.9);
    margin-left: 20px;
    margin-right: 20px; }
    .header a #HOME {
      animation: intial-appearance 0.5s forwards ease-in-out; }

.quote {
  font-family: "Georgia";
  text-align: left;
  color: black; }
  .quote #SMALL {
    font-size: 14px;
    padding-bottom: 20px;
    line-height: 1.2;
    opacity: 0;
    animation: initial-appearance 0.7s forwards 0.5s ease-in-out; }
  .quote #BIG {
    font-size: 19px;
    letter-spacing: 0.5px;
    padding-bottom: 10px;
    line-height: 1.2;
    opacity: 0;
    animation: initial-appearance 0.7s forwards 1s ease-in-out; }

@media only screen and (max-width: 700px) {
  .header nav {
    display: none; }

  .bubble-button #COLLABORATE {
    display: none; } }
@keyframes initial-slide-up {
  from {
    transform: translateY(30%);
    opacity: 0; }
  to {
    transform: translateY(0);
    opacity: 1; } }
@keyframes initial-slide-right {
  from {
    transform: translateX(-10%);
    opacity: 0; }
  to {
    transform: translateX(0);
    opacity: 1; } }
@keyframes initial-appearance {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }
